body {
  background-color: rgba(136, 238, 105, 0.671);
}
header {
  background-color: rgb(255, 255, 255);
  position: absolute;
  top: 0;
  display: flex;
  justify-content: flex-end;
  width: 100%;
  height: 10%;
  align-items: center;
  gap: 2%;

  box-shadow: 0px 5px rgba(31, 31, 31, 0.678);
}
header h1 {
  padding: 5px;
}

.item-btn {
  color: white;
  font-weight: medium;
  background-color: red;
  border-style: none;
}
.item-btn:hover {
  color: red;
  background-color: white;
  border: 1px solid red;
}

.btn:focus {
  outline: none;
  box-shadow: none;
}

.btn-check:focus + .btn-primary,
.btn-primary:focus,
.search-btn:hover,
.search-map-btn:hover {
  color: red;
  background-color: white;
  border: 1px solid red;
}
.leaflet-container {
  position: absolute;

  width: 100%;
  height: 100%;
}
.logo {
  position: absolute;
  left: 0px;
  width: 17%;
  height: auto;
}
.logo img {
  position: relative;
  width: 30%;
}
.items {
  position: absolute;
  top: 15%;

  display: grid;
  width: 100%;
  justify-content: center;
  grid-template-columns: 25% 25% 25%;
  column-gap: 8%;
}
.item {
  padding: 0;
  margin: 7%;
  width: 100%;
  height: 100%;
}
.card {
  border-radius: 20px;
  box-shadow: 0px 0px 50px 0px rgba(0, 0, 0, 0.623);
}
.item img {
  border-radius: 20px 20px 0px 0px ;
  position: relative;
  max-height: 300px;
  box-shadow: 0px 4px rgba(31, 31, 31, 0.288);
}
.item p {
  font-weight: semi-bold;
}
.not-found {
  font-weight: bold;
  justify-content: center;
  position: absolute;
  left: 8%;
  top: 20%;
}
.search-map-btn,
.search-btn {
  height: 50px;
  color: white;
  font-weight: bold;
  background-color: red;
  border-style: none;
  margin: 10px;
  position: relative;
  right: 0;
}
.search-map-btn {
  width: 100%;
}
.search-btn {
  width: 4%;
}
